<template>
  <div v-loading="divLoading">

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>过滤查询</span>
      </div>
      <div>
        <el-form :inline="true" :model="formInline_fiter" class="demo-form-inline" style="margin-left: 10px">
          <el-form-item label="筛选手机号">
            <el-input v-model="formInline_fiter.phoneNum" placeholder="请输入手机进行筛选"></el-input>
          </el-form-item>
          <el-form-item label="筛选银行卡">
            <el-input v-model="formInline_fiter.bankNum" placeholder="请输入银行卡进行筛选" ></el-input>
          </el-form-item>
          <el-form-item style="margin-left: 5px">
            <el-button type="primary" @click="whereQuery" icon="el-icon-search">过滤搜索</el-button>
          </el-form-item>
          <el-form-item style="margin-left: 5px">
            <el-button style="background: #33cabb;border: #33cabb;color:#ffffff" @click="addInfo" icon="el-icon-plus">添加内容</el-button>
          </el-form-item>
          <el-form-item style="margin-left: 5px">
            <el-button style="background: #926dde;border: #926dde;color:#ffffff" @click="refreshInfo" icon="el-icon-refresh">刷新</el-button>
          </el-form-item>
        </el-form>
      </div>

    </el-card>

    <el-card class="box-card" style="margin-top: 20px">
      <div slot="header" class="clearfix">
        <span>信息库</span>
      </div>
      <div>
        <el-table
            :data="adminData"
            style="width: 100%;margin-bottom: 10px"
            border
            fit
            highlight-current-row
            :height="tableHeight"
        >

          <el-table-column prop="id" label="ID" width="100" align="center">
            <template slot-scope="{row}">
              <span>{{ row.ID }}</span>
            </template>
          </el-table-column>

          <el-table-column prop="big_remark" label="手机号"  align="center" min-width="100px">
            <template slot-scope="{row}">
              <span>{{ row.Phone }}</span>
            </template>
          </el-table-column>

          <el-table-column prop="remark" label="银行卡"  align="center" min-width="100px">
            <template slot-scope="{row}">
              <span>{{ row.Card }}</span>
            </template>
          </el-table-column>

          <el-table-column label="创建时间" min-width="100" align="center" >
            <template slot-scope="{row}">
              <span>{{ row.Created | parseTime}}</span>
            </template>
          </el-table-column>

          <el-table-column prop="mode_price" label="操作" align="center" width="300">
            <template slot-scope="{row}">
<!--              <el-button-->
<!--                  size="mini"-->
<!--                  @click="handledetailbtn(row)"-->
<!--                  icon="el-icon-search"-->
<!--                  style="font-size: 13px;font-weight:bold;background-color:#6739B6;border-color:#6739B6;color: #ffffff;"-->
<!--              >-->
<!--                查看-->
<!--              </el-button>-->
<!--              <el-button-->
<!--                  size="mini"-->
<!--                  @click="handlechangebtn(row)"-->
<!--                  icon="el-icon-edit"-->
<!--                  style="font-size: 13px;font-weight:bold;background-color:#115195;border-color:#115195;color: #ffffff;"-->
<!--              >-->
<!--                修改-->
<!--              </el-button>-->
              <el-button
                  size="mini"
                  @click="handledelbtn(row)"
                  icon="el-icon-document-delete"
                  style="font-size: 13px;font-weight:bold;background-color:#FF5722;border-color:#FF5722;color: #ffffff;"
              >
                删除
              </el-button>
            </template>
          </el-table-column>

        </el-table>

        <el-pagination
            :total="total"
            background
            layout="total, sizes, prev, pager, next, jumper"
            :page-sizes="[10,30,50,100, 200, 1000, 2000]"
            :page-size.sync ="limit_pageSize"
            :current-page.sync ="current_page"
            @size-change="sizeChange"
            @current-change="currentChange"
        />

        <!--这里 dialog 查看模板内容-->
<!--        <el-dialog-->
<!--            title="查看模板内容"-->
<!--            :visible.sync="dialogLookVisible"-->
<!--            width="80%"-->
<!--            :close-on-click-modal="false"-->
<!--            :close-on-press-escape="false"-->
<!--            @close="beforeClosLook"-->
<!--            :append-to-body="true"-->
<!--            top="2%"-->
<!--        >-->
<!--          &lt;!&ndash;        在组件标签中加v-if来控制组件el-dialog的出现与否,每次弹出el-dialog，都会执行子组件的mounted,而不加v-if只会执行一次生命周期钩子函数。&ndash;&gt;-->
<!--          <ChangePrice :rowData="rowData"  v-if="dialogLookVisible" />-->
<!--        </el-dialog>-->

        <!--这里 dialog 修改模板内容-->
        <el-dialog
            title="添加信息内容"
            :visible.sync="dialogAddVisible"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            @close="beforeCloseEdit"
            :append-to-body="true"
            width="40%"
            top="2%"
        >
          <el-form :model="ability" :label-position="labelPosition" label-width="120px">

            <el-form-item label="手机号-银行卡">
              <el-input v-model="ability.phone"  placeholder="请在此输入手机号-银行卡"></el-input>
            </el-form-item>

<!--            <el-form-item label="银行卡" style="width: 300px">-->
<!--              <el-input v-model="ability.card" placeholder="请在此输入银行卡"></el-input>-->
<!--            </el-form-item>-->

          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogAddVisible = false">取 消</el-button>
            <el-button type="primary" @click="conFirmAddClick">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </el-card>

  </div>
</template>

<script>
import {parseTime} from "../utils/constant"
import {
  getAddInfoData,
  getDeletInfoData,
  getInfoList,
  getInfoListFilter,
} from "../api";

export default {
  name: "TableAdmin",
  // components:{
  //   ChangePrice
  // },
  filters:{
    parseTime(data){
      // console.log("data",parseTime(data))
      return parseTime(data,'{y}-{m}-{d} {h}:{i}')
    }
  },
  data(){
    return{
      formInline_fiter:{
        phoneNum:'',
        bankNum:''
      },
      ability:{
        phone:'',
        card:''
      },
      dialogAddVisible:false,
      tableHeight: window.innerHeight - 330,
      formInline:{
        petAddreessID:null
      },
      adminData:[],
      total:0,
      limit_pageSize:50,
      current_page:1,
      divLoading:false,
      uploadUrl:'',
      rowData: {},
      dialogEditVisible:false,
      labelPosition: 'right',

    }
  },
  methods:{
    // beforeClosLook(){//查看模板内容关闭回调事件
    //   this.dialogLookVisible = false
    // },
    // async readdata(){ //读取数据
    //   let queryID;
    //   if(this.formInline.petAddreessID.indexOf('axie') === -1){ //没有找到该字符串，即代表是数字ID
    //     queryID = this.formInline.petAddreessID
    //   }else{//找到该字符串，即代表是链接
    //     queryID = getStr(this.formInline.petAddreessID,'.com/axie/','/')
    //   }
    //
    //   this.divLoading = true
    //   let backModelResult = await backModel(queryID)
    //   this.divLoading = false
    //   // console.log("backModelResult",backModelResult)
    //   if(backModelResult.code === 0){
    //     this.$notify({
    //       title: '成功',
    //       message: '读取数据成功',
    //       type: 'success'
    //     });
    //
    //     this.adminData = backModelResult.data
    //
    //   }else{
    //     this.$notify.error({
    //       title: '错误',
    //       message: backModelResult.msg
    //     });
    //   }
    //
    //
    // },
    // openSelect(){
    //
    // },
    // // loadFile(){
    // //
    // // },
    // uploadBefore(){
    //   this.divLoading = true
    // },
    // uploadSuccess(response,file,fileList){
    //   this.divLoading = false
    //   // console.log("response,file,fileList",response,file,fileList)
    //   if(response.code === 200){
    //     this.$notify({
    //       title: '成功',
    //       message: response.msg,
    //       type: 'success'
    //     });
    //     this.adminData = []
    //     this.getList(this.current_page,this.limit_pageSize)
    //
    //   }
    // },
    // uploadError(err,file,fileList){
    //   this.divLoading = false
    //   // console.log("err,file,fileList",err,file,fileList)
    //   this.$notify.error({
    //     title: '错误',
    //     message: err.msg
    //   });
    // },

    async getList(action,page,limit){//获取数据列表
      // selecting
      let result = await getInfoList(action,page,limit)
      // console.log("result",result)
      if(result.code === 1){
        this.adminData = result.result
        this.total = result.count
      }
    },
    sizeChange(number){ //pageSize改变时会触发(每页显示条目个数改变时会触发)
      // console.log("sizeChange",number)
      this.limit_pageSize = number

      if(this.formInline_fiter.phoneNum === '' && this.formInline_fiter.bankNum === ''){
        this.getList('GET',this.current_page,this.limit_pageSize)
      }else {
        this.whereQuery()
      }

    },
    currentChange(number){//currentPage 改变时会触发
      // console.log("currentChange",number)
      this.current_page = number
      if(this.formInline_fiter.phoneNum === '' && this.formInline_fiter.bankNum === ''){
        this.getList('GET',this.current_page,this.limit_pageSize)
      }else {
        this.whereQuery()
      }

    },
    // async changeSwitch(switchValue,row){ //模板开关点击事件
    //   console.log("switchValue,row",switchValue,row)
    //   let result = await changeSwitch(row.id,switchValue)
    //   // console.log("result",result)
    //   if(result.code === 200){
    //
    //     this.$notify({
    //       title: '成功',
    //       message: result.msg,
    //       type: 'success'
    //     });
    //
    //   }else{
    //     this.$notify.error({
    //       title: '错误',
    //       message: result.msg
    //     });
    //   }
    //
    // },
    // handledetailbtn(row){
    //   this.rowData = row
    //   this.dialogLookVisible = true
    // },

    beforeCloseEdit(){//操作栏添加对话框关闭点击事件
        this.dialogAddVisible = false
    },
    async whereQuery(){ //过滤搜索按钮点击事件
      let requestParam = {}
      requestParam.action = "GET"
      requestParam.page = this.current_page
      requestParam.limit = this.limit_pageSize
      if(this.formInline_fiter.phoneNum !== '' || !this.formInline_fiter.phoneNum){
        requestParam.phone = this.formInline_fiter.phoneNum
      }
      if(this.formInline_fiter.bankNum !== '' ){
        requestParam.card = this.formInline_fiter.bankNum
      }else{
        // console.log("值为空")
      }
      let getWcModelFilter_result = await getInfoListFilter(requestParam)
      if(getWcModelFilter_result.code === 1){
        this.adminData = getWcModelFilter_result.result
        this.total = getWcModelFilter_result.count
      }

    },
    handledelbtn(row){ //操作栏删除按钮点击事件
      const rowID = row.ID
      this.$confirm('系统将删除当前信息数据，确认要删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(async() => {
        let deleteModel_result = await getDeletInfoData('DEL',rowID)
        if(deleteModel_result.code === 200){
          this.whereQuery()
          this.$notify({
            title: '成功',
            message: deleteModel_result.msg,
            type: 'success'
          });

        }else{
          this.$notify({
            title: '错误',
            message: deleteModel_result.msg,
            type: 'error'
          });
        }
      }).catch(() => {


      });
    },
    addInfo(){ //添加内容
      this.dialogAddVisible = true
    },
    async conFirmAddClick(){ //确认添加按钮
      if(this.ability.phone === '' || !this.ability.phone){

        this.$notify({
          title: '错误',
          message: '内容不能为空!',
          type: 'error'
        });
        return false
      }

      // if(this.ability.card === '' || !this.ability.card){
      //
      //   this.$notify({
      //     title: '错误',
      //     message: '银行卡不能为空!',
      //     type: 'error'
      //   });
      //   return false
      // }

      let contentArray = this.ability.phone.split('-')
      console.log("contentArray",contentArray,contentArray.length)
      if(contentArray.length !== 2 ){

        this.$notify({
          title: '错误',
          message: '内容格式错误,请重新填写!',
          type: 'error'
        });
        return false
      }

      if(contentArray[0] === ''){

        this.$notify({
          title: '错误',
          message: '手机为空,请重新填写!',
          type: 'error'
        });
        return false
      }

      if(contentArray[1] === ''){

        this.$notify({
          title: '错误',
          message: '银行卡为空,请重新填写!',
          type: 'error'
        });
        return false
      }

      let result = await getAddInfoData('ADD',contentArray[0],contentArray[1])
      // console.log("result",result)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });
        this.dialogAddVisible = false
        this.whereQuery()
      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    refreshInfo(){
      this.getList('GET',this.current_page,this.limit_pageSize)
    }

  },
  created() {

    // this.handleSwitchShop()
    // this.handleMonitoring()
    // this.handleSupervisePrice()
    // this.uploadUrl = IMG_PATH
    this.getList('GET',this.current_page,this.limit_pageSize)
  },
  computed:{

  },
  mounted() {

  },
  beforeDestroy() { //vue销毁之前

  }
}
</script>

<style >

/* switch按钮样式 */
.switchStyle_input .el-switch__label {
  position: absolute;
  display: none;
  color: #fff !important;
}
/*打开时文字位置设置*/
.switchStyle_input .el-switch__label--right {
  z-index: 1;
}
/* 调整打开时文字的显示位子 */
.switchStyle_input .el-switch__label--right span{
  /*margin-right: 9px;*/
  margin-left: 17px;
}
/*关闭时文字位置设置*/
.switchStyle_input .el-switch__label--left {
  z-index: 1;
}
/* 调整关闭时文字的显示位子 */
.switchStyle_input .el-switch__label--left span{
  margin-left: 25px;
}
/*显示文字*/
.switchStyle_input .el-switch__label.is-active {
  display: block;
}
/* 调整按钮的宽度 */
.switchStyle_input.el-switch .el-switch__core,
.el-switch .el-switch__label {
  width: 70px !important;
  margin: 0;
}

</style>
